.category-history-container {
  position: relative;
  min-height: 200px;
  padding: 2rem;
  margin: 0 auto;

  .search-section.card {
    border-radius: 12px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.06);
    border: 1px solid var(--neutral-lighter);
    padding: 1.5rem;

    h3 {
      color: var(--primary-dark);
      margin-bottom: 1rem;
      font-weight: 600;
    }
  }

  .btn-primary {
    background-color: var(--primary);
    color: white;
    border-radius: 8px;
    &:hover {
      background-color: var(--primary-dark);
    }
  }

  .history-list.card {
    border-radius: 12px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.06);
    border: 1px solid var(--neutral-lighter);
    padding: 1.5rem;

    h2 {
      color: var(--primary-dark);
      margin-bottom: 1rem;
      font-weight: 600;
    }
  }

  .history-list {
    .table-responsive {
      overflow-x: auto;
      border-radius: 8px;
    }

    .table thead th {
      background-color: var(--secondary);
      color: white;
      font-weight: 600;
      padding: 0.875rem 1rem;
    }

    .table tbody tr:hover {
      background-color: var(--primary-lightest);
    }
  }
}

.search-container {
  margin-bottom: 1rem;

  .search-form {
    display: flex;
    gap: 1rem;
  }

  .search-inputs {
    display: flex;
    gap: 1rem;
    width: 100%;
    flex-wrap: wrap;
    align-items: center;

    .form-control {
      border: 1px solid var(--neutral-lighter);
      border-radius: 8px;
      min-height: 42px;
      &:focus {
        border-color: var(--primary);
        box-shadow: 0 0 0 2px rgba(0, 173, 218, 0.2);
        outline: none;
      }
    }

    input[type="date"].form-control {
      min-width: 140px;
    }

    select.form-control {
      min-width: 120px;
    }

    .search-btn {
      height: 42px;
    }

    @media (max-width: 768px) {
      flex-direction: column;
      align-items: stretch;
    }
  }
}

.table-responsive {
  margin: 1rem 0;
}

.text-center {
  text-align: center;
  padding: 1rem;
  color: var(--text-secondary);
}
